.wrapper {
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  /* grid-template-columns: repeat(auto-fill, minmax(200px, 1fr)); */
  grid-gap: 4px;
  grid-auto-rows: minmax(100px, auto);
}

ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  /* background-color: #333333; */
}

li {
  padding-left: 40px;
  float: left;
}

li a {
  /* display: block;
  color: #FFFF;
  text-align: center;
  padding: 16px; */
  text-decoration: none;
}

/* li a:hover {
  background-color: #111111;
} */

/* li a:visited {
  color: red;
} */

.one {
  grid-column: 2 / span 6;
  grid-row: 1;

  @media only screen and (max-width: 600px)  {
    grid-column: 1 / span 6;
    grid-row: 1;
  }
}
.two { 
  grid-column: 1 / 9;
  grid-row: 2 / span 5;
  /* background-color: blue; */
}
.three {
  grid-column: 1/ span 8;
  grid-row: 7;
  background-color: #f2f2f2;
}
/* .four {
  grid-column: 1/span 8;
  grid-row: 8 / 11;
  background-color: #FFFF;
} */
.four {
  grid-column: 1/span 8;
  grid-row: 8 / span 1;
  background-color: #f2f2f2;
}
.five {
  grid-column: 1/span 8;
  grid-row: 9;
  /* background-color: brown; */
}

@media only screen and (max-width: 600px)  {

  .one {
    grid-column: 1 / span 8;
    grid-row: 1;
    display: flex;
    flex: 1 1;
    padding-left: 20px;
    flex-wrap: wrap;
  }

}

@media only screen and (max-width: 768px)  {

  .one {
    grid-column: 1 / span 8;
    grid-row: 1;
    display: flex;
    flex: 1 1;
    padding-left: 20px;
    flex-wrap: wrap;
  }

}
